cb2>3.平均五年内拿到绿卡</p><p data-v-05b05cb2>4.花费少，无投资风险</p><p data-v-05b05cb2>5.一人申请，配偶及21周岁以下子女同时获得美国永久绿卡</p><p data-v-05b05cb2>6.排队期间，申请人可继续从事国内工作，不必着急赴美</p></div></div></div></div></div><div class=\"feature\" data-v-05b05cb2><div class=\"feature-desc\" data-v-05b05cb2><div class=\"title\" data-v-05b05cb2>其他增值服务</div><div class=\"content\" data-v-05b05cb2><p data-v-05b05cb2><strong class=\"green-text\" data-v-05b05cb2>SOC撰写</strong></p><div class=\"content-item\" data-v-05b05cb2><img src=\"" + _imports_1 + "\" class=\"content-img\" data-v-05b05cb2><div data-v-05b05cb2><p data-v-05b05cb2>1.与客户沟通SOC撰写思路</p><p data-v-05b05cb2>2.帮助客户总结归纳申请闪光点</p><p data-v-05b05cb2>3.给客户交付SOC材料助力客户绿卡申请</p></div></div></div><div class=\"content\" data-v-05b05cb2><p data-v-05b05cb2><strong class=\"green-text\" data-v-05b05cb2>Notable Citation撰写</strong></p><div class=\"content-item\" data-v-05b05cb2><img src=\"" + _imports_2 + "\" class=\"content-img\" data-v-05b05cb2><div data-v-05b05cb2><p data-v-05b05cb2>1.遍历客户的所有文章和引用</p><p data-v-05b05cb2>2.总结突出客户工作的重要性（是怎么被其他文献引用的）</p><p data-v-05b05cb2>3.一周内返工，节省客户大量时间精力</p></div></div></div><div class=\"content\" data-v-05b05cb2><p data-v-05b05cb2><strong class=\"green-text\" data-v-05b05cb2>背景提升</strong></p><div class=\"content-item\" data-v-05b05cb2><img src=\"" + _imports_3 + "\" class=\"content-img\" data-v-05b05cb2><div data-v-05b05cb2><p data-v-05b05cb2>1.高质量期刊审稿邀约</p><p data-v-05b05cb2>2.精挑行业精英＋内推牛人作为推荐人</p></div></div></div></div></div></div></div>", 1)

